  color: black;
  background-color: white;
}

.header-reservation {
  min-height: 30rem;
  background-image: url("/build/img/pexels-elevate-1267315.webp");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.header-reservation .mask {
  min-height: 30rem;
  background-color: rgba(0, 0, 0, 0.5);
}

@media (max-width: 480px) {
  .reservationEvents .actions {
    flex-direction: column;
    gap: 5rem;
    align-items: flex-start;
  }
  .reservationEvents .actions .logOutIcon {
    order: 1;
  }
  .reservationEvents .actions .logOutButton {
    order: 2;
  }
}

.bg-reservation {
  background-color: #6E899D;
  border-radius: 1rem;
  padding: 2rem;
}

.newReservation {
  box-shadow: -1px 1px 19px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px 1px 19px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 1px 19px 0px rgba(0, 0, 0, 0.75);
  width: 100%;
  transition: all 0.2s ease-in-out;
  min-height: 30rem;
  border-radius: 1rem;
  position: fixed;
  z-index: 1000;
  bottom: -100%;
  background-color: white;
}

.card-reservation {
  box-shadow: -5px 6px 21px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -5px 6px 21px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -5px 6px 21px 0px rgba(0, 0, 0, 0.75);
  background-color: white;
  border-radius: 1rem;
  margin: 0 auto;
  margin-bottom: 2rem;
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  min-height: 20rem;
}
.card-reservation p {
  color: black;
}
.card-reservation span {
  color: black;
}
.card-reservation .infoCard {
  margin-bottom: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}

.reservations .show_less {
  height: 0;
  width: 0;
  opacity: 0;
  visibility: hidden;
  position: fixed;
}
.reservations .Options {
  position: absolute;
}
.reservations .containerOptions {
  box-shadow: -3px 5px 11px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -3px 5px 11px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -3px 5px 11px 0px rgba(0, 0, 0, 0.75);
  z-index: 2;
  width: max-content;
  bottom: 6rem;
  right: 9rem;
  position: relative;
  background-color: #333333;
  padding: 2rem;
}
.reservations .containerOptions p {
  text-align: start;
  color: white;
  font-size: 2rem;
}
.reservations .infoAdded {
  bottom: 7rem;
  right: 11rem;
}

.showFilters {
  padding: 2rem;
  bottom: -200%;
  right: 0;
  width: 20rem;
}

.container-error {
  transition: all 0.1s ease-in;
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.container-error .error {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  background-color: #36393B;
  border-radius: 1rem;
  max-width: 40rem;
  padding: 4rem;
}
@media (min-width: 480px) {
  .container-error .error {
    max-width: 50rem;
  }
}
.container-error .error p {
  color: white;
}
.container-error .error svg {
  height: 3rem;
  width: 3rem;
  filter: invert(0);
}
.container-error .error .header_error {
  font-size: 3rem;
  font-weight: 700;
  color: white;
}
.container-error .error .body_error {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.container-error .error .body_error p {
  font-size: 2rem;
}
.container-error .error .footer_error {
  cursor: pointer;
  display: inline-block;
  background-color: #6E899D;
  padding: 1rem 3rem;
  border-radius: 1rem;
  text-align: center;
}
.container-error .error .container_svg {
  height: 3rem;
  width: 3rem;
}
.container-error .spinner {
  border-radius: 1rem;
  max-width: 50rem;
  padding: 4rem;
}

.footer {
  width: 100%;
  bottom: 0;
  padding: 1rem;
}
@media (min-width: 480px) {
  .footer {
    position: absolute;
  }
}
.footer .bar-footer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  padding: 2rem;
}
@media (min-width: 480px) {
  .footer .bar-footer {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    gap: 0;
  }
}
.footer .bar-footer .copyright {
  font-weight: 400;
}
.footer .bar-footer .powered {
  font-weight: 600;
}

.menu-bar {
  visibility: hidden;
  height: 0;
  width: 0;
  position: fixed;
}
@media (max-width: 768px) {
  .menu-bar {
    visibility: visible;
    height: 5.6rem;
    transition: all 0.2s ease-in;
    background-color: white;
    box-shadow: -1px -10px 14px -9px #000;
    z-index: 999;
    width: 100%;
    bottom: 0;
  }
  .menu-bar a {
    color: black;
  }
  .menu-bar svg {
    height: 3rem;
    width: 3rem;
    stroke: #6E899D;
  }
}
@media (max-width: 768px) {
  .menu-bar .container-bar {
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .menu-bar .container-bar .icons {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
@media (max-width: 768px) {
  .menu-bar .show-menu {
    max-width: 36rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  .menu-bar .show-menu .icons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
  .menu-bar .show-menu .icons .icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  .menu-bar .show-menu .icons .icon img {
    filter: invert(1);
    height: 3rem;
    width: 3rem;
  }
}

.sk-chase {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}

.sk-chase-dot:before {
  content: "";
  display: block;
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  }
  100%, 0% {
    transform: scale(1);
  }
}